@import"https://fonts.googleapis.com/css2?family=Cairo+Play&family=Lato&family=Titillium+Web:wght@300&display=swap";:root{--default-font: "Lato", sans-serif;--heading-font: "Titillium Web", sans-serif;--logo-font: "Cairo Play", sans-serif}:root{--background-color: #ffffff;--primary-color: #e84545;--secondary-color: #32353a}*,*::before,*::after{box-sizing:border-box;margin:0;padding:0}body{font-family:var(--default-font);font-size:large;cursor:default}.event-finder{font-family:var(--logo-font)}h2{font-family:var(--heading-font);font-size:50px}.btn{background-color:var(--primary-color);color:#fff;transition:.3s}.btn:hover{background-color:var(--primary-color);color:#fff;opacity:.7}.btn:focus{background-color:var(--primary-color);color:#fff;opacity:.7}.btn img{cursor:pointer}.section-title{padding:80px 0px 40px 0px;text-align:center}.section-title h2::before,.section-title h2::after{content:"—";margin:10px;color:var(--primary-color);opacity:.8}@media screen and (max-width: 420px){.section-title h2::before,.section-title h2::after{content:"–";margin:5px}}@media screen and (max-width: 350px){.section-title h2::before,.section-title h2::after{content:""}}.scroll-top{position:fixed;bottom:20px;right:20px;width:48px;height:48px}